import blog from '@/api/blog'

export default {
  data () {
    return {
      blogs:[],
      user:{},
      page:1,
      total:0
    }
  },
  created(){
    this.userId = this.$route.params.userId
    this.page = this.$route.query.page || 1
    blog.getBlogsByUserId(this.userId,{page:this.page})
      .then(res =>{
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
        if(res.data.length > 0){
          this.user = res.data[0].user
        }
      })
  },
  methods:{
    onPageChange(newPage){
      blog.getBlogsByUserId(this.userId,{page:newPage})
      .then(res =>{
        this.blogs = res.data
        this.page = res.page
        this.total = res.total
        this.$route.push({path:`/user/${this.userId}`,query:{page:newPage}})
      })
    },

    splitDate(dataStr){
      let dataObj = typeof(dataStr) === 'object' ? dataStr : new Date(dataStr)
      return{
        date: dataObj.getDate(),
        month: dataObj.getMonth() + 1,
        year: dataObj.getFullYear()
      }
    }
  }
}


